.navbar{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    height: 100px;
    align-items: center;
    background-color: #2f2f2f;
    color: white;
    box-sizing: border-box;
    overflow: hidden;
}
.navbar_left{
    display: flex;
    max-width: 100%;
    align-items: center;
}
.navbar_left p{
    padding-left: 10px;
    padding-right: 10px;
    font-size: 14px;
    box-sizing: border-box;
    cursor: pointer;
    transition: 0.3s ease-in-out;

}
.navbar_left > p:hover{
    color:  #e9ff00;
}
.navbar_right > p:hover{
    color:  #e9ff00;
}
.shopping_cart{
  cursor: pointer;
}
.box{
  display: flex;
  align-items: center;

}
.box p{

  background-color:#e9ff00;
  color: black;
  border-radius: 5px;
}
.shopping_cart:hover{
  color:  #e9ff00;
}

.navbar_right{
    display: flex;
    max-width: 100%;
    align-items: center;
}

.navbar_right p{
    padding-left: 10px;
    padding-right: 10px;
    font-size: 14px;
    cursor: pointer;
    transition: 0.3s ease-in-out;
}


.navbar_logo{
    display: flex;
    align-items: center;
}
.navbar_logo img{
    width: 250px;
    height: 100px;
    cursor: pointer;
}
.bg img{
    width: 600px;
}
@keyframes shake {
    0% {
      transform: translateX(0);
    }
    20% {
      transform: translateX(-10px) rotate(-5deg);
    }
    40% {
      transform: translateX(10px) rotate(5deg);
    }
    60% {
      transform: translateX(-10px) rotate(-5deg);
    }
    80% {
      transform: translateX(10px) rotate(5deg);
    }
    100% {
      transform: translateX(0);
    }
  }

  /* Apply the animation to the image */
  .navbar_logo img {
    animation: shake 12s infinite;
  }
